<template>
  <ul>
    <gnb-sub-item
      v-for="(item, index) in items"
      :key="index"
      :title="item.title"
      :link="item.link"
      :subItems="item.subItems"
      :index="index"
      :current-index="currentIndex"
      @toggle="toggleAccordion"
    />
  </ul>
</template>

<script>
import GnbSubItem from '@/components/lms/common/GnbSubItem.vue';

export default {
  components: { GnbSubItem },
  props: {
    items: Array,
  },
  data() {
    return {
      currentIndex: null,
    };
  },
  methods: {
    toggleAccordion(index) {
      this.currentIndex = this.currentIndex === index ? null : index;
    },
  },
};
</script>