import { createApp } from 'vue'
import App from './App.vue'
import { router } from '@/router/index.js'

const app = createApp(App)
app.use(router)


// -------- LMS ----------
import store from '@/store'
import '@/assets/lms/style/css/reset.min.css'
import '@/assets/lms/style/css/common.css'
import '@/assets/lms/style/css/style.css'
import lmsCommon from '@/api/lmsCommon.js';

app.use(store).use(lmsCommon)

// popup
import LmsPopup from '@/components/lms/popup/LmsModal.vue'
app.component('LmsPopup', LmsPopup)
app.mixin({
  methods: {
    openPopup(id, data) {
      this.$store.dispatch('openPopup', { id, data });
    },
  },
});
// -------- LMS ----------


app.mount('#app')
