<template>
  <div class="viewMain">
    <mainNav></mainNav>
    <section class="banner_contents_wrap">
      <div class="ban_wrap slide slide_wrap">
        <div class="slide_item item1">
          <img src="@/assets/user/img/top_ban01.jpg" alt="MainBanner">
        </div>
        <div class="slide_item item2">
          <img src="@/assets/user/img/top_ban02.jpg" alt="MainBanner">
        </div>
        <div class="slide_item item3">
          <img src="@/assets/user/img/top_ban03.jpg" alt="MainBanner">
        </div>
        <div class="slide_prev_button slide_button" @click="prevSlide"></div>
        <div class="slide_next_button slide_button" @click="nextSlide"></div>
        <ul class="slide_pagination"></ul>
      </div>


      <div class="main_cont">
        <div class="main_cont_tit">미래혁신기술의 중심</div>
        <div class="main_cont_sub_tit">차세대통신사업단</div>
        <div class="text_center"></div><!--text_center-->
      </div><!--main_cont-->
    </section><!--banner_contents_wrap-->

    <mainFooter></mainFooter>
    <div>


    </div>
  </div>
</template>

<script>
import mainNav from "@/components/main/navComponents.vue";
import mainFooter from "@/components/main/footerComponents.vue"

export default {
  name: 'viewMain',
  props: {},
  data(){
    return {
      currentSlide: 0,
      totalSlides: 3 // 슬라이드 총 개수
    };
  },
  components : {
    mainNav,mainFooter,
  },
  methods: {
    nextSlide() {
      this.currentSlide = (this.currentSlide + 1) % this.totalSlides;
    },
    prevSlide() {
      this.currentSlide = (this.currentSlide - 1 + this.totalSlides) % this.totalSlides;
    }
  },
}
</script>

<style scoped>


.viewMain{
  margin: 0 auto;
  padding: 0;
  width: 100%;
  height: 100%;
}



section {
  width: 100%;
  height: 650px;
  background-color: lightgray;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}


</style>
