<!-- components/ReusablePopup.vue -->
<template>
  <div v-if="isPopupOpen" class="popup fix flex__c__m" @click="closePopupOutside">    
    <div class="popup-content flex__c__m tc rel"> 
      <div class="pop-btn">
        <button @click="closePopup" type="button">확인</button>
      </div>
      <button @click="closePopup" type="button" class="abs">Close</button>
    </div>
  </div>
</template>

<script>
export default {
  props: ['popupId'],
  computed: {
    isPopupOpen() {
      return this.$store.getters.getPopupById(this.popupId) !== undefined;
    },
  },
  methods: {
    closePopup() {
      this.$store.dispatch('closePopup', this.popupId);
    },
    closePopupOutside(event) {
      if (event.target.classList.contains('popup')) {
        this.closePopup();
      }
    },    
  }
};
</script>